@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
	font-size: 2.5rem;
	text-align: center;
	margin-top: 0.5rem;
	text-transform: uppercase;
	border-bottom: 0.1rem solid black;
}

.banner{
	background-image: url(/www.architectpartners.cf/static/media/banner.f3583a41.jpg);
	background-size: cover;
	height: 100%;

}

img {
	width: 100%;
}
.footer {
	display: flex;
	flex-direction: column;
	align-items: center;
	border-top: 0.1rem solid black

}


.footer-nav{
	flex:2 1;
	font-size: 1rem;
	margin:0.5rem;
	display: flex;
	justify-content: space-evenly;
	width: 100%;
	border-bottom: 0.1rem solid black;
	text-transform: uppercase;
	transition: .4s;

}

.footer-nav:hover {
	padding-bottom: 1rem;
	font-size:1.2rem;
	transition: .4s
}

.links{
	transition: .4s;
	font-weight: 400;
}

.footer-nav:hover .links{
	padding: 0.1rem;
	transition: 0.4s;
}

.links:hover{
	transition: 0.5s;
	font-weight: 600;
	border-bottom: 0.1rem solid black;	
	
	border-width: 0.2rem;
}

.footer-logo{
	margin: 0.5rem;
	flex:1 1;
}

img{
	width: 5rem;
}
.section-projects{
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.projects{
  height: 100%;
  display: flex;
flex-direction: column;
justify-content: center;
align-content: center;
min-height: 70vh;
}

.porjects-header{
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.projects-container{
  width: 100%;
  justify-content: space-evenly;
  flex:5 1;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 5rem;
}
.project{
  margin-bottom: 2rem;
  width: 30%;
  padding: 0.2rem;
  display: flex;
  flex-direction: column;
  transition: 0.4s ease-out;
  align-items: flex-end;
  position: relative;
}

.project:hover{
    transform: translateY(20px);
    cursor: pointer;
}

.project:before{
   content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2;
  transition: 0.5s;
  opacity: 0;
}
.project:hover:before{
  opacity: 1;
}


.project:hover .project-info{
 opacity: 1;
  transform: translateY(0px);
  display: flex;

}

.project-img-container{
  padding: 0.1rem;
  height: 100%;
}

.project-img{
   width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 15px;
}
.project-info{
  display: flex;
  flex-direction: column;
  transition: 0.5s;
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
}
.project-info{
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
}

h1{
	text-align: center;
}
.form-container{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-content: space-between;
}


input{
	padding: 1em;
	margin-bottom: 3em;
}


.input-message{
	width: 90%;
	height: 20em;
}

button{
	width: 30%;
	height: 5em;
	margin-bottom: 2em;
	border-color: green;
	font-size: 1em;
}
.about-container{

	display: flex;
	flex-direction: row;
}

p{
	width: 30%;
	font-size: 1.3em;
	margin:0.5em;
	text-align: center;
}
